.duanslider
    margin-top: 60px
    display: flex
    align-items: center
    .swiper-container
        position: relative
        width: 100%
        padding: 50px 0
        .swiper-wrapper
            .swiper-slide
                position: relative
            .swiper-slide:not(active)
                &:after
                    width: 100%
                    height: 100%
                    position: absolute
                    content: ''
                    background: rgba(0, 0,0, .63)
                    left: 0
                    bottom: 0
                .item 
                    height: 450px
                    width: auto
                    img 
                        width: 100%
                        height: 100%
                        object-fit: cover
            
        .control
            position: absolute
            left: 50%
            transform: translateX(-50%)
            top: 275px
            z-index: 1
            display: flex
            justify-content: flex-start
            flex-direction: row-reverse
            width: 460px
            .swiper-btn-next
                margin-left: auto
                outline: none
                em 
                    font-size: 30px
                    color: #ebebeb
            .swiper-btn-prev
                outline: none
                em 
                    font-size: 30px
                    color: #ebebeb
        @media (max-width: 576px)
            .control
                width: 70px
                bottom: 10px
.box-text
    padding: 60px
    .tt
        text-transform: uppercase
    p 
        padding-top: 20px
